.App {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.todo-form {
  display: flex;
  margin-bottom: 20px;
}

.todo-form input {
  flex: 1;
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-right: 10px;
}

.todo-form button {
  padding: 8px 16px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.todo-list {
  list-style: none;
  padding: 0;
}

.todo-list li {
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: #f9f9f9;
  margin-bottom: 5px;
  border-radius: 4px;
}

.todo-list li.completed span {
  text-decoration: line-through;
  color: #888;
}

.todo-list li input[type="checkbox"] {
  margin-right: 10px;
}

.todo-list li button {
  margin-left: auto;
  padding: 5px 10px;
  background-color: #ff4444;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.filter-buttons {
  margin-bottom: 20px;
}

.filter-buttons button {
  margin-right: 10px;
  padding: 5px 10px;
  background-color: #ddd;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.filter-buttons button.active {
  background-color: #4CAF50;
  color: white;
}

h1 {
  color: #333;
  text-align: center;
}

/* Стили для светлой темы (по умолчанию) */
.light-mode {
  background-color: #ffffff;
  color: #333333;
}

.light-mode .todo-list li {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
}

.light-mode input {
  background-color: #ffffff;
  color: #333333;
}

/* Стили для тёмной темы */
.dark-mode {
  background-color: #333333;
  color: #ffffff;
  min-height: 100vh;
}

.dark-mode .todo-list li {
  background-color: #444444;
  border: 1px solid #555555;
  color: #ffffff;
}

.dark-mode input {
  background-color: #444444;
  color: #ffffff;
  border-color: #555555;
}

.dark-mode .filter-buttons button {
  background-color: #444444;
  color: #ffffff;
}

.dark-mode .filter-buttons button.active {
  background-color: #4CAF50;
}

.theme-switch {
  position: absolute;
  top: 20px;
  right: 20px;
}

.theme-switch button {
  padding: 8px 16px;
  background-color: transparent;
  border: 2px solid #4CAF50;
  border-radius: 20px;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.3s ease;
}

.dark-mode .theme-switch button {
  border-color: #4CAF50;
  color: #ffffff;
}

.light-mode .theme-switch button {
  border-color: #4CAF50;
  color: #333333;
}

.theme-switch button:hover {
  background-color: #4CAF50;
  color: white;
}

/* Обновляем стили для заголовка */
.light-mode h1 {
  color: #333333;
}

.dark-mode h1 {
  color: #ffffff;
}
